body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #F8F8FF;
    height: 100%;
}

html,
body{
    height: 100%;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 50px;
}

.movies .movie {
    width: 45%;
    height: 60%;
    padding: 10px;
    margin: 11px;
    display: flex;
    align-items: flex-start;
    background-color: white;
    box-shadow: 0px 5px 10px rgb(201, 199, 199);

}

.movies img{
    max-width: 120px;
    position: relative;
    top: -20px;
    box-shadow: 0px 5px 10px rgb(201, 199, 199);

}

.movies .movie__data{
    padding-left: 10px;
}

.movies .movie__title{
   font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

.movies .movie__year{
    color: grey;
}

.movie__summary{
    color: rgb(122, 122, 122);
}

.genres{
    height: 60px;
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
}